@import url("https://fonts.cdnfonts.com/css/acherus-grotesque");
@font-face {
  font-family: "Regular Brush";
  font-weight: bold;
  src: url("/font/Regular Brush.otf") format("opentype");
}
*,
*:after,
*:before {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  text-decoration: none;
}

body {
  font-family: "Regular Brush", sans-serif;
  font-size: 16px;
}

html .container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 768px) {
  html .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  html .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  html .container {
    max-width: 1140px;
  }
}
@media (min-width: 1400px) {
  html .container {
    max-width: 1320px;
  }
}
header.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 40px 15px;
}
header.header nav.navigation {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
header.header nav.navigation ul {
  list-style-type: none;
  display: flex;
  gap: 30px;
}
header.header nav.navigation ul li a {
  font-size: 2em;
  color: #FFFFFF;
}
header.header div.logo {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
header.header div.logo a {
  display: block;
}
header.header div.logo a img {
  height: 130px;
}
header.header div.access {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}
header.header div.access ul {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  gap: 30px;
}
header.header div.access ul > li a {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
header.header div.access ul > li a img {
  width: 100%;
}
header.header div.access ul > li a span {
  text-align: center;
  font-family: "Acherus Grotesque", sans-serif;
  font-size: 0.9375em;
  color: #FFFFFF;
}

@media only screen and (min-width: 992px) {
  header.header nav.navigation div.hamburger {
    display: none;
  }
}
@media only screen and (max-width: 992px) {
  header.header {
    padding: 20px 15px;
  }
  header.header nav.navigation div.hamburger a {
    display: flex;
    flex-direction: column;
  }
  header.header nav.navigation div.hamburger a span {
    font-family: "Acherus Grotesque", sans-serif;
    text-align: center;
  }
  header.header nav.navigation ul {
    display: none;
  }
  header.header div.access ul {
    gap: 15px;
  }
  header.header div.access ul > li a img {
    width: 30px;
    height: 30px;
  }
  header.header nav.navigation div.hamburger a img {
    width: 40px;
    height: 40px;
  }
  header.header nav.navigation div.hamburger a,
  header.header div.access ul > li a {
    align-items: center;
  }
  header.header nav.navigation div.hamburger a span,
  header.header div.access ul > li a span {
    font-size: 0.75em;
  }
}
section.main-highlight {
  background: #13120D;
  margin-top: -215px;
}
section.main-highlight div.wrap-highlight {
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
section.main-highlight div.wrap-highlight div.featured-products {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  margin-top: 107.5px;
}
section.main-highlight div.wrap-highlight div.featured-products div.item-highlight {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 15px;
}
section.main-highlight div.wrap-highlight div.featured-products div.item-highlight a {
  display: block;
  z-index: 100;
}
section.main-highlight div.wrap-highlight div.featured-products div.item-highlight a img {
  width: 100%;
}
section.main-highlight div.wrap-highlight div.featured-products div.item-highlight div.highlight-info h2 {
  font-size: 3.5em;
  color: #FFFFFF;
  margin-bottom: 30px;
}
section.main-highlight div.wrap-highlight div.featured-products div.item-highlight div.highlight-info a.info-view {
  display: block;
  text-align: right;
  margin-top: 15px;
}
section.main-highlight div.wrap-highlight div.featured-products div.item-highlight a.info-buy {
  display: block;
  font-family: sans-serif;
  font-weight: bold;
  background: #457C05;
  border-radius: 7px;
  padding: 20px 40px;
  text-transform: uppercase;
  color: #FFFFFF;
}
section.main-highlight div.wrap-highlight div.featured-products div.item-highlight a.info-view {
  font-family: sans-serif;
  text-transform: lowercase;
  color: #FFFFFF;
  font-weight: 600;
}
section.main-highlight div.wrap-highlight div.featured-products div.item-highlight.highlight-left {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}
section.main-highlight div.wrap-highlight div.featured-products div.item-highlight.highlight-left div.highlight-info a.info-buy {
  width: calc(100% + 120px);
  text-align: left;
}
section.main-highlight div.wrap-highlight div.featured-products div.item-highlight.highlight-left div.highlight-info a.info-view {
  padding-left: 50px;
  text-align: left;
}
section.main-highlight div.wrap-highlight div.featured-products div.item-highlight.highlight-right {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
section.main-highlight div.wrap-highlight div.featured-products div.item-highlight.highlight-right div.highlight-info a.info-buy {
  margin-left: -120px;
  width: calc(100% + 120px);
  text-align: right;
}
section.main-highlight div.wrap-highlight div.featured-products div.item-highlight.highlight-right div.highlight-info a.info-view {
  padding-right: 50px;
  text-align: right;
}

div.buy-even {
  margin: 60px auto 0 auto;
}
div.buy-even a {
  display: block;
  background: #457c05;
  color: #fff;
  font-family: sans-serif;
  font-weight: 700;
  padding: 20px 60px;
  text-transform: uppercase;
  text-align: center;
  -webkit-border-radius: 7px;
  border-radius: 7px;
  font-size: 1.25em;
}

@media only screen and (max-width: 1200px) {
  section.main-highlight div.wrap-highlight {
    height: unset;
  }
  section.main-highlight div.wrap-highlight div.featured-products {
    display: flex;
    flex-direction: column;
    margin-top: 215px;
  }
  section.main-highlight div.wrap-highlight {
    padding-bottom: 100px;
  }
}
@media only screen and (max-width: 992px) {
  section.main-highlight div.wrap-highlight {
    height: unset;
  }
  section.main-highlight div.wrap-highlight div.featured-products {
    display: flex;
    flex-direction: column;
    margin-top: 250px;
  }
  section.main-highlight div.wrap-highlight div.featured-products div.item-highlight.highlight-left {
    display: flex;
    flex-direction: column;
  }
  section.main-highlight div.wrap-highlight div.featured-products div.item-highlight.highlight-left div.highlight-info {
    text-align: left;
    width: 100%;
  }
  section.main-highlight div.wrap-highlight div.featured-products div.item-highlight.highlight-right {
    display: flex;
    flex-direction: column;
  }
  section.main-highlight div.wrap-highlight div.featured-products div.item-highlight.highlight-right div.highlight-info {
    text-align: right;
    width: 100%;
  }
  section.main-highlight div.wrap-highlight div.featured-products div.item-highlight div.highlight-info h2 {
    width: 100%;
    font-size: 2.875em;
    text-align: left;
    margin-left: 25px;
    margin-bottom: 15px;
  }
  section.main-highlight div.wrap-highlight div.featured-products div.item-highlight.highlight-left div.highlight-info a.info-view {
    padding-left: 0;
    width: 70%;
    text-align: center;
  }
  section.main-highlight div.wrap-highlight div.featured-products div.item-highlight.highlight-left div.highlight-info a.info-buy {
    width: 70%;
    display: block;
    text-align: center;
  }
  section.main-highlight div.wrap-highlight div.featured-products div.item-highlight.highlight-right div.highlight-info h2 {
    text-align: right;
    padding-right: 25px;
    margin-left: 0;
  }
  section.main-highlight div.wrap-highlight div.featured-products div.item-highlight.highlight-right div.highlight-info {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  section.main-highlight div.wrap-highlight div.featured-products div.item-highlight.highlight-right div.highlight-info a.info-buy {
    display: block;
    text-align: center;
    width: 70%;
    margin-left: 0;
  }
  html section.main-highlight div.wrap-highlight div.featured-products div.item-highlight.highlight-right div.highlight-info a.info-view {
    padding-left: 0px;
    text-align: center;
    padding-right: 0;
    display: block;
    width: 70%;
  }
  section.main-highlight div.wrap-highlight div.featured-products div.item-highlight a img {
    width: 70%;
  }
  section.main-highlight div.wrap-highlight div.featured-products div.item-highlight.highlight-right > a {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin-top: -150px;
  }
}
section.first-presentation {
  background-color: #333;
  padding: 100px 0;
}
section.first-presentation div.wrap-first-presentation {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
section.first-presentation div.wrap-first-presentation h2 {
  text-align: center;
  font-size: 3em;
  color: #FFFFFF;
  margin-bottom: 30px;
  margin-top: 30px;
}
section.first-presentation div.wrap-first-presentation p {
  color: #FFFFFF;
  font-size: 1.25em;
  font-weight: 300;
  text-align: center;
  font-family: sans-serif;
  line-height: 1.5em;
  width: 80%;
  margin: 0 auto;
}
section.first-presentation div.wrap-first-presentation div.presentation-buy {
  width: 100%;
  text-align: center;
  margin-top: 30px;
}
section.first-presentation div.wrap-first-presentation div.presentation-buy a {
  display: block;
  background: #457c05;
  color: #fff;
  font-family: sans-serif;
  font-weight: 700;
  padding: 20px 60px;
  text-transform: uppercase;
  text-align: center;
  -webkit-border-radius: 7px;
  border-radius: 7px;
  font-size: 1.25em;
  width: 50%;
  margin: 0 auto;
}

@media only screen and (max-width: 1200px) {
  section.first-presentation div.wrap-first-presentation {
    width: 100%;
  }
}
@media only screen and (min-width: 992px) {
  section.first-presentation div.wrap-first-presentation h2.presentation-title-mobile {
    display: none;
  }
}
@media only screen and (max-width: 992px) {
  section.first-presentation div.wrap-first-presentation {
    width: 100%;
  }
  section.first-presentation div.wrap-first-presentation h2.presentation-title-mobile {
    display: block;
  }
  section.first-presentation div.wrap-first-presentation article.presentation-content h2 {
    display: none;
  }
}